@import "./globals.scss";
.sidebar {
  //   border-top: 3px solid white;
  width: 300px;
  height: 85vh;
  position: sticky !important;
  // left: 0;
  top: 10%;
  background-color: rgba($color: $primaryColorLight, $alpha: 0.9);
  margin: 20px 0;
  border-radius: 0 20px 20px 0;
  display: flex;
  flex-direction: column;
  box-shadow: 1px 1px 10px rgba($color: $white, $alpha: 0.6);
  hr {
    background-color: $white;
  }
  h3 {
    color: $white;
    font-family: "Lato";
    font-weight: 500;
  }
  .sidebar-nav {
    flex: 1;
    display: flex;
    flex-direction: column;
    // justify-content: space-between;
    // height: 10;
    list-style: none;
    // padding: 0;
    .navs {
      a {
        text-decoration: none;
        .sidebar-nav-item {
          color: $whitish;
          margin: 10px 0;
          font-family: "Lato";
          font-size: 1.2rem;
          .sidebar-icon {
            color: $white;
            transform: scale(1.5);
            margin: 0 10px;
          }
        }
      }
    }
    .logout-btn {
      // margin: 0;
      color: $whitish;
      margin: 10px 0;
      font-family: "Lato";
      font-size: 1.2rem;
      .sidebar-icon {
        color: $white;
        transform: scale(1.5);
        margin: 0 10px;
      }
    }
    .extend {
      flex: 1;
    }
  }

  // &::after {
  //   content: ">";
  //   width: 50px;
  //   height: 50px;
  //   background-color: $white;
  //   position: absolute;
  //   top: 50%;
  //   right: 0;
  //   transform: translate(100%, -50%);
  // }
}
